<template>
  <div>
    <div v-for="(item,idx) in data" :key="item.name" @click="select">
      <span class="dir-item" :data-url="item.url||''" :data-idx="idx">{{item.name}}</span>
      <span v-if="item.dir" v-for="sub in item.dir" :class="'dir-sub '+(!item.show?'':'dir-sub-show')" :key="sub.name" :data-url="sub.url||''">{{sub.name}}</span>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  props: ["data"],
    methods: {
        select(e) {
            let url = e.target.dataset.url || '';
            let index = e.target.dataset.idx;
            if(url) {
                this.$emit('select',url);
            } else if(typeof index != 'undefined') {     
                let temp = this.data[index];
                temp.show = !temp.show;
                this.data.splice(index,1,temp);
            }
        }
    }
};
</script>
<style>
.dir-item {
  display: block;
  font-size: 18px;
  line-height: 2;
  cursor: pointer;
}

.dir-sub {
  display: none;
  padding-left: 10px;
  font-size: 14px;
  line-height: 2;
  color: #676767;
  cursor: pointer;
}
.dir-sub-show {
    display: block;
}
.dir-item:hover,.dir-sub:hover {
    color: #4067d6;
}
</style>