export function loadJs(url,name) {
    return new Promise(resolve => {
        if(name && window[name]) {
            resolve();
        } else {
            const jsDom = document.createElement('script');
            jsDom.src = url;
            document.body.appendChild(jsDom);
            jsDom.onload = function() {
                resolve();
            }
        }    
    });
}