<template>
  <div id="app">
    <directory :class="directoryStyle" :data="directoryData" @select="loadMd"></directory>
    <div :class="contentStyle">
      <i class="icon-menu" @click="switchMenu"></i>
      <div v-html="markedHtml"></div>
    </div>
  </div>
</template>

<script>
import { loadJs } from "./common";
import marked from "marked";
import directory from './components/Directory';
export default {
  data() {
    return {
      markedHtml: "",
      directoryData: [],
      menuStatus: 1
    };
  },
  name: "app",
  components: {
    directory
  },
  mounted() {
    loadJs("https://unpkg.com/axios/dist/axios.min.js", "axios")
      .then(() => {
        this.loadDir();
      })
      .then(() => {
        this.loadMd("/md/1.md");
      }) 
  },
  methods: {
    loadMd(url) {
      axios.get(url)
      .then(res => {
        this.markedHtml = marked(res.data);
      })
      .catch(err => {
        console.log('err:',err);
      });
    },
    loadDir() {
      axios.get("/md/dir.js").then(res => {
        this.directoryData = res.data.dir;
      });
    },
    switchMenu() {
      this.menuStatus = 1 - this.menuStatus;
    }
  },
  computed: {
    directoryStyle() {
      if(this.menuStatus) {
        return ['directory'];
      } else {
        return ['directory','directory-hidden'];
      }
    },
    contentStyle() {
      if(!this.menuStatus) {
        return ['content'];
      } else {
        return ['content','content-with-menu'];
      }
    }
  }
};
</script>

<style>
@import "./css/markdown.css";
@import "./css/icon.css";
* {
  margin: 0;
  padding: 0;
}
body,html {
  width: 100%;
  height: 100%;
}
#app {
  position: relative;
  width: 100%;
  height: 100%;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  overflow: hidden;
}
.directory {
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  width: 300px;
  height: 100%;
  padding: 30px 15px 0 15px;
  border-right: 1px solid #dedede;
  background: #fafafa;
  transition: left .2s ease;
  overflow: hidden;;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.directory-hidden {
  left: -300px;
}
.content {
  position: relative;
  height: 100%;
  box-sizing: border-box;
  padding: 10px 0 30px 30px;
  overflow: auto;
}
.content-with-menu {
  margin-left: 300px;
}
.content > div {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}
.icon-menu {
  position: absolute;
  width: 16px;
  height: 16px;
  top: 5px; 
  left: 10px;
  font-size: 16px;
  cursor: pointer;
}
</style>
